.page {
  scroll-behavior: smooth;

  font-family: Manrope, sans-serif;
  font-size: 16px;
  line-height: 22.4px;
  color: #1b2129;

  &__body {
    min-width: 320px;
    margin: 0;
  }

  &__menu {
    pointer-events: none;

    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    transform: translateX(-100%);

    opacity: 0;

    transition: all 0.3s;

    &:target {
      pointer-events: all;
      transform: translateX(0%);
      opacity: 1;
    }
  }
}

.page:has(.page__menu:target) {
  overflow: hidden; /* not to scroll the page */
}
